.home_intro {
    position: relative;
    &__dna {
        display: block;
        max-width: min(33%, 510px);
        margin-inline: auto;
        position: absolute;
        left: 50%;
        transform: translate(-50%, -50%);
        // transform-origin: left top;
        // animation: rotatoes 20s linear infinite;
        top: 50px;
        @media (min-width: 768px) {
            top: 0;
        }
    }
    &__lines {
        display: block;
        max-width: 68%;
        margin-inline: auto;

        padding-top: 95px;
        @media (min-width: 992px) {
            padding-top: 145px;
        }
    }

    &__card {
        display: grid;
        gap: 0.5rem;
        grid-template-rows: auto auto 1fr auto;
        justify-content: center;
        height: 100%;
    }

    &__icon {
        max-width: min(185px, 50vw);
        margin-inline: auto;
    }
    .h4 {
        max-width: 14ch;
        line-height: 1.2;
        margin-inline: auto;
        margin-bottom: 0.25rem;
    }
    .fs-300 {
        max-width: 35ch;
        margin-inline: auto;
    }

    &__brace {
        width: 100%;
    }

    @media (max-width: 768px) {
        .col-md-4.padme {
            margin-top: 140px;
        }
    }
}

@keyframes rotatoes {
    0% {
        rotate: 0deg;
    }
    100% {
        rotate: 360deg;
    }
}
